/**
 * Get the full toString value string based on type
 *
 * @param {string} type
 * @returns {string}
 */
export const getObjectClass = (type) => {
  return `[object ${type}]`;
};

/**
 * @function
 *
 * @description
 * Get the toString of the object
 *
 * @param {any} object
 * @returns {string}
 */
export const toString = (object) => {
    return Object.prototype.toString.call(object);
};



